* {
  box-sizing: border-box;
  text-decoration: none;
}
*:focus {
  outline: 0;
}
*:hover {
  text-decoration: none;
}
*:focus {
  outline: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
.link {
  color: #477cc5;
  cursor: pointer;
  border-bottom: 1px solid rgba(71,124,197,0.2);
  text-decoration: none;
  transition: all 0.4s;
}
.link:hover {
  color: #3a6fb7;
  border-color: rgba(71,124,197,0.5);
}
html {
  font-size: 62.5%;
  margin: 0;
  max-width: 100%;
  padding: 0;
  width: 100%;
}
body {
  color: #111;
  background: #fff;
  font-family: 'Roboto', 'Arial', 'Helvetica', sans-serif;
  font-weight: 400;
  font-size: 1.6em;
  line-height: 1;
  margin: 0;
  max-width: 100%;
  padding: 0;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  width: 100%;
}
body * {
  box-sizing: border-box;
}
.btn {
  background: #eb251c;
  border: 0;
  border-radius: $radius;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: 'Roboto', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  font-weight: 600;
  flex-shrink: 0;
  line-height: 1;
  letter-spacing: 0.4px;
  max-height: 6rem;
  padding: 2.2rem 3.5rem;
  text-decoration: none;
  text-transform: uppercase;
  transition: all 0.4s;
}
.btn__icon {
  line-height: 1 !important;
  margin-bottom: 0 !important;
}
.btn__icon .fa {
  font-weight: 600;
  line-height: 1;
  margin: 0;
}
span + .btn__icon {
  margin-left: 2rem;
}
.btn__icon + span {
  margin-left: 2rem;
}
.btn:hover {
  background-color: #cd1a12;
}
.btn--solid {
  background-color: transparent;
  color: #eb251c;
  border: 2px solid #eb251c;
}
.btn--solid:hover {
  border-color: #cd1a12;
  color: #fff;
}
.btn--full-size {
  padding: 2.2rem 3rem;
  text-align: center;
  width: 100%;
}
.btn--white {
  background-color: transparent;
  border: 1px solid #fff;
  color: #fff;
  margin-bottom: 2rem;
}
.btn--small {
  padding: 1.6rem 3.6rem;
}
.flatpickr-month {
  margin-bottom: 1rem;
}
.flatpickr-calendar {
  padding: 1.5rem 0;
}
.flatpickr-calendar.arrowTop:before,
.flatpickr-calendar.arrowBottom:before,
.flatpickr-calendar.arrowTop:after,
.flatpickr-calendar.arrowBottom:after {
  display: none !important;
}
.form-control {
  min-height: 5rem;
  margin-bottom: 1.6rem;
  position: relative;
}
.form-control__icon {
  color: #868686;
  font-size: 3.2rem;
  position: absolute;
  right: 2rem;
  top: 3rem;
}
.form-control__icon .fa {
  transition: all 0.2s;
}
.form-control label {
  color: #111;
  cursor: text;
  display: inline-block;
  line-height: 1;
  font-weight: 300;
  margin-bottom: 0.8rem;
  transition: all 0.2s;
  max-width: 100%;
}
.form-control input[type="text"],
.form-control input[type="password"],
.form-control input[type="date"],
.form-control input[type="email"],
.form-control input[type="tel"],
.form-control input[type="number"],
.form-control select,
.form-control textarea,
.form-control .select2-selection--single {
  background: #fff;
  border: 2px solid #eee;
  border-radius: 0;
  display: block;
  font-size: 1.6rem;
  line-height: 1;
  min-height: 4rem;
  transition: border-color 0.2s;
  padding: 1.3rem 1.6rem;
  position: relative;
  resize: vertical;
  width: 100%;
  -webkit-appearance: none;
}
.form-control input[type="text"]:focus,
.form-control input[type="password"]:focus,
.form-control input[type="date"]:focus,
.form-control input[type="email"]:focus,
.form-control input[type="tel"]:focus,
.form-control input[type="number"]:focus,
.form-control select:focus,
.form-control textarea:focus,
.form-control .select2-selection--single:focus {
  border-color: #eb251c;
}
.form-control input[type="text"]:focus + .form-control__icon,
.form-control input[type="password"]:focus + .form-control__icon,
.form-control input[type="date"]:focus + .form-control__icon,
.form-control input[type="email"]:focus + .form-control__icon,
.form-control input[type="tel"]:focus + .form-control__icon,
.form-control input[type="number"]:focus + .form-control__icon,
.form-control select:focus + .form-control__icon,
.form-control textarea:focus + .form-control__icon,
.form-control .select2-selection--single:focus + .form-control__icon {
  transform: rotate(180deg);
}
.form-control textarea {
  height: 100%;
  min-height: 22rem;
}
.form-control .select2-container--open + .form-control__icon {
  transform: rotate(180deg);
}
.form-control--error input[type="text"],
.form-control--error input[type="password"],
.form-control--error input[type="date"],
.form-control--error input[type="email"],
.form-control--error input[type="tel"],
.form-control--error input[type="number"],
.form-control--error select,
.form-control--error textarea,
.form-control--error .select2-selection--single {
  border-color: $error;
}
.form-control--error label {
  color: $error;
}
.form-control--error .form-control__icon {
  color: $error;
}
.custom-select--type-2 {
  font-size: 3.8rem !important;
  padding: 1rem 3rem !important;
  padding-right: 6rem !important;
}
.custom-select--type-2 + .form-control__icon {
  top: 0.5rem;
}
.checkbox {
  cursor: pointer;
  display: block;
  margin-bottom: 1rem;
  user-select: none;
}
.checkbox__label {
  background: transparent;
  border-radius: $radius;
  border: 2px solid #eee;
  cursor: pointer;
  height: 2.4rem;
  width: 2.4rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  vertical-align: middle;
  z-index: 1;
}
.checkbox__label:after {
  color: #eb251c;
  content: "\f00c";
  display: none;
  font-family: 'FontAwesome';
  font-size: 1.5rem;
  left: 3px;
  position: absolute;
  top: 2px;
  z-index: 10;
}
.checkbox input {
  display: none;
}
.checkbox input:checked + .checkbox__label:after {
  display: block;
}
.checkbox label {
  color: #111;
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
}
.radio {
  cursor: pointer;
  display: block;
  margin-bottom: 1rem;
  user-select: none;
}
.radio__label {
  background: transparent;
  border-radius: 50%;
  border: 1px solid #d6d6d6;
  cursor: pointer;
  height: 2.4rem;
  width: 2.4rem;
  display: inline-block;
  margin-right: 1rem;
  position: relative;
  vertical-align: middle;
  z-index: 1;
}
.radio__label:after {
  background-color: #eb251c;
  border-radius: 50%;
  height: 1.4rem;
  width: 1.4rem;
  color: #fff;
  content: '';
  display: none;
  font-size: 1.5rem;
  left: 4px;
  position: absolute;
  top: 4px;
  z-index: 10;
}
.radio input {
  display: none;
}
.radio input:checked + .radio__label {
  background-color: #fff;
  border-color: #eb251c;
  box-shadow: 0 0 0 1px #eb251c;
}
.radio input:checked + .radio__label:after {
  display: block;
}
.radio label {
  color: #111;
  display: inline-block;
  cursor: pointer;
  vertical-align: middle;
}
.file-upload {
  display: block;
  margin-bottom: 0.8rem;
  margin-top: 0.8rem;
}
.file-upload input {
  display: none;
}
.table {
  border-collapse: collapse;
  font-size: 1.4rem;
  font-weight: 300;
  max-width: 100%;
  margin-bottom: 2rem;
  margin-top: 2rem;
  width: 100%;
}
.table thead {
  background: #fef4f4;
  text-align: left;
}
.table thead th {
  color: #eb251c;
  font-weight: 400;
}
.table th,
.table td {
  line-height: $line-height;
  padding: 1.2rem 3rem;
  padding-right: 1rem;
  margin: 0;
  vertical-align: top;
}
.table th:first-child,
.table td:first-child {
  min-width: 7rem;
}
.table tr:nth-child(even) {
  background: #fef4f4;
}
.table tfoot td {
  border: none;
  font-weight: 600;
}
.table i {
  margin: 0 !important;
}
h1 {
  margin: 0;
}
.h1,
.h2,
.h3,
.p,
p {
  margin: 0;
  margin-bottom: 1rem;
}
.h1 {
  font-family: 'Neris', 'Arial', 'Helvetica', sans-serif;
  font-size: 6rem;
  font-weight: 800;
  line-height: 1.1;
  margin-bottom: 3.5rem;
  text-transform: uppercase;
}
.h2 {
  font-size: 4.8rem;
  font-family: 'Neris', 'Arial', 'Helvetica', sans-serif;
  font-weight: 800;
  line-height: 1;
  margin-bottom: 2rem;
  text-transform: uppercase;
}
.h3 {
  font-family: 'Neris', 'Arial', 'Helvetica', sans-serif;
  font-size: 3.2rem;
  font-weight: 800;
  line-height: 1.2;
  margin-bottom: 2rem;
}
.h4 {
  display: inline-block;
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.5;
  margin-bottom: 1.5rem;
  margin-top: 0;
}
.h5 {
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: 0.4px;
  margin-bottom: 2.4rem;
  margin-top: 0;
