}
.index .p,
.inner .p {
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.65;
  margin-bottom: 3rem;
}
.index .strong,
.inner .strong {
  display: inline-block;
  font-size: 1.6rem;
  font-weight: 400;
  margin-bottom: 0.5rem;
}
.index .i,
.inner .i {
  color: #868686;
  display: inline-block;
  font-family: 'Neris', 'Arial', 'Helvetica', sans-serif;
  font-weight: 400;
  line-height: 1.6;
  margin-bottom: 3.5rem;
}
.index blockquote,
.inner blockquote {
  color: #eb251c;
  display: block;
  font-weight: 300;
  line-height: $line-height;
  margin-bottom: 4rem;
  margin-left: 0;
  padding-left: 3.5rem;
  position: relative;
}
.index blockquote:before,
.inner blockquote:before {
  background-color: #eb251c;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 2px;
}
.inner .p + ul {
  margin-top: -2.4rem;
}
.inner ul {
  margin-bottom: 2.4rem;
  padding-left: 2rem;
}
.inner ul li {
  color: #111;
  line-height: 1.6;
  list-style-type: none;
  margin-bottom: 0.8rem;
  position: relative;
}
.inner ul li:after {
  background-color: #eb251c;
  height: 4px;
  width: 4px;
  content: '';
  position: absolute;
  left: -1.9rem;
  top: 12px;
}
.inner sup {
  font-size: 60%;
  top: -0.7em;
}
.ul-offset {
  padding-left: 6rem !important;
}
.text-primary {
  color: #eb251c;
}
.uppercase {
  text-transform: uppercase;
}
@media (max-width: 500px) {
  .h3 {
    line-height: 1;
  }
}
