.icon {
  background-size: 100%;
  display: inline-block;
  vertical-align: middle;
}
.icon.icon--edit {
  background: url("../../img/edit.png") no-repeat;
  height: 2rem;
  width: 2rem;
  transform: translateY(-2px);
}
.modal {
  animation: bigFadeUp 0.3s forwards ease;
  background: #fff;
  box-shadow: 0 1rem 2rem rgba(17,17,17,0.1);
  border-radius: $radius 0 $radius $radius;
  display: none;
  margin: 10rem auto;
  padding: 4rem;
  position: relative;
  width: 72rem;
}
.modal__title {
  font-size: 2.4rem;
  font-weight: 500;
  line-height: 1;
  margin-bottom: 4rem;
}
.modal__plate {
  background-color: #eee;
  margin-left: -4rem;
  margin-right: -4rem;
  margin-bottom: 3.2rem;
  margin-top: -1.6rem;
  padding: 1.6rem 4rem;
}
.modal--without-border {
  background-color: transparent;
  box-shadow: none;
  padding: 0;
  max-width: 70%;
  height: 40rem;
}
.modal--without-border iframe {
  height: 100%;
  width: 100%;
}
.modal--without-border .mfp-close,
.modal--without-border .mfp-close:active {
  color: #fff !important;
  font-size: 3.2rem;
  opacity: 1;
  right: -5rem !important;
  top: -1.3rem !important;
}
.mfp-ready .modal {
  display: block;
}
.mfp-close-btn-in .mfp-close,
.mfp-close-btn-in .mfp-close:active {
  height: 4rem;
  width: 4rem;
  color: #111;
  font-size: 4rem;
  opacity: 1;
  padding: 0;
  text-align: center;
  top: 2.9rem;
  right: 2.4rem;
}
.mfp-close {
  height: 6rem;
  width: 6rem;
}
.open-image {
  display: block;
}
.mfp-gallery .mfp-close,
.mfp-gallery .mfp-close:active {
  color: #fff;
  right: -5.6rem;
  top: 2.8rem;
}
@media (max-width: 767px) {
  .mfp-container {
    padding: 0;
  }
  .modal {
    padding: 2.4rem 4%;
    right: 0;
    width: 94%;
  }
  .modal__plate {
    margin-left: -4.3%;
    margin-right: -4.3%;
    padding: 1.6rem 4%;
  }
  .modal .mfp-close,
  .modal .mfp-close:active {
    font-size: 4rem;
    opacity: 1;
    top: 1.4rem;
    right: 0.4rem;
  }
}
.slick-prev,
.slick-next {
  top: 50%;
}
.slick-prev:before,
.slick-next:before {
  font-size: 2.4rem;
  color: #fff;
  transition: color 0.2s;
}
.slick-prev {
  left: -3rem;
}
.slick-prev:before {
  content: '\f104';
  font-family: 'FontAwesome';
}
.slick-next {
  right: -3rem;
}
.slick-next:before {
  content: '\f105';
  font-family: 'FontAwesome';
}
.hero .slick-dotted.slick-slider {
  margin-bottom: 0;
}
.hero .slick-dots {
  bottom: 3rem;
}
.hero .slick-dots li button:before {
  border: 1px solid #fff;
  border-radius: 50%;
  height: 1.6rem;
  width: 1.6rem;
  content: '';
  opacity: 1;
}
.hero .slick-dots li.slick-active button:before {
  background-color: #fff;
}
.inner-text-slider .slick-prev,
.inner-text-slider .slick-next {
