}
.inner-text-slider .slick-prev:before,
.inner-text-slider .slick-next:before {
  color: #eb251c;
}
.inner-text-slider .slick-prev {
  left: -3rem;
}
.inner-text-slider .slick-next {
  right: -3rem;
}
.actual-slider {
  margin: 0 -1rem;
}
.actual-slider img {
  width: 100%;
}
.actual .slide__img {
  padding: 0 1rem;
}
.actual .slick-prev,
.actual .slick-next {
  height: 4rem;
}
.mCS-dark.mCSB_scrollTools .mCSB_dragger .mCSB_dragger_bar {
  background-color: #eb251c !important;
}
.mCS-dark.mCSB_scrollTools .mCSB_dragger:hover .mCSB_dragger_bar {
  background-color: #eb251c !important;
}
.hero {
  background-color: #111;
  height: calc(100vh - 10rem);
  max-height: 80rem;
  margin-bottom: 7rem;
  overflow: hidden;
  position: relative;
}
.hero__video {
  opacity: 0.5;
  position: absolute;
  width: 100%;
}
.hero__video video {
  max-width: 100%;
  width: 100%;
}
.hero__container {
  align-items: center;
  color: #fff;
  display: flex;
  height: 100%;
  justify-content: center;
  position: relative;
  width: 100%;
  z-index: 50;
}
.hero__content {
  padding: 0 8rem;
}
.hero .logo__icon {
  background-image: url("../../img/logo-white-big.png");
  height: 106px;
  width: 136px;
}
.hero .logo__text {
  font-size: 12.4rem;
  margin-top: 2rem;
  margin-bottom: 3.5rem;
}
.hero__subtitle {
  font-size: 1.8rem;
  line-height: 1.6;
  padding-left: 18.1500872600349%;
}
.index__section {
  margin-bottom: 12rem;
}
.index .about {
  font-size: 2.2rem;
  font-weight: 300;
}
.index__info-img {
  position: relative;
  transform: translateY(-2rem);
}
.index__info-img img {
  display: block;
  max-width: 100%;
}
.index__info-img .info {
  color: #fff;
  left: 6rem;
  position: absolute;
  top: 2rem;
}
.main-page-img {
  transform: translateX(-3rem);
}
.service {
  align-items: flex-end;
  background-color: #111;
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  height: 22rem;
  margin-bottom: 3rem;
  transition: background-size 0.3s;
}
.service__container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  margin-left: 2rem;
  margin-bottom: 1.5rem;
  width: 100%;
}
.service__title {
  color: #fff;
  display: inline-block;
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.2;
  margin-right: 1rem;
  text-transform: uppercase;
  vertical-align: middle;
}
.service__btn {
  background-color: #eb251c;
  height: 4rem;
  width: 4rem;
  color: #fff;
  display: block;
  flex-shrink: 0;
  margin-right: 2rem;
  text-align: center;
  transition: transform 0.3s ease;
}
.service__btn .fa {
  font-size: 2.2rem;
  font-weight: 600;
  margin-top: 0.8rem;
}
.service:hover .service__btn {
  transform: translateX(-2rem);
}
.news {
  margin-bottom: 3.2rem;
  margin-top: 3.2rem;
}
.news .new__container {
  min-height: 15rem;
}
.news .new__img {
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
  min-height: 24rem;
  margin-bottom: 1.6rem;
}
.news .new__title {
  font-size: 2rem;
  font-weight: 500;
  line-height: 1.3;
  margin-bottom: 1.6rem;
  text-transform: uppercase;
}
.news .new__anons {
  font-size: 1.4rem;
  line-height: 1.6;
}
.news .new__date {
  color: #868686;
}
@media (max-width: 1200px) {
  .hero__content {
    padding: 0;
  }
  .hero__video {
    background: url("../../video/main.jpg");
    background-size: cover;
    height: 100%;
    opacity: 0.4;
  }
  .hero__video video {
    display: none;
  }
  .news .new__container {
    padding: 2rem;
