  transition: transform 0.4s ease;
}
.mobile-menu-toggle:before {
  content: '';
  left: -3.5rem;
  position: absolute;
  top: -3rem;
  height: 8rem;
  width: 8rem;
}
.mobile-menu-toggle span {
  background-color: #fff;
  display: block;
  height: 2px;
  position: absolute;
  top: 0;
  transition: all 0.3s;
  width: 3rem;
}
